@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500;700;900&display=swap');

body {
	background: #666;
	font-family: 'Roboto', sans-serif;
}

.container {
	padding-top: 5rem;
	width: 800px;
	margin: 0 auto;
}

.card {
	border-radius: 6px;
	background: #fff;
	padding: 1rem;
	box-shadow: 2px 3px 10px rgba(0, 0, 0, 0.3);	
}

.btn {
	color: #42b983;
	position: relative;
	border-radius: 99px;
	border: 1px solid #42b983;
	text-decoration: none;
	text-transform: uppercase;
	padding: 0.5rem 1.5rem;
	font-weight: 700;
	outline: none;
	background: #fff;
	transition: all 0.22s;
	cursor: pointer;
	margin-right: 1rem;
}

.btn.primary {
	background: #42b983;
	color: #fff
}

.btn:active {
	box-shadow: inset 1px 1px 1px rgba(0, 0, 0, 0.3);
}

.btn:hover {
	cursor: pointer;
	opacity: 0.8;
}

input[type='file'] {
	display: none;
}

.preview {
	display: flex;
	flex-wrap: wrap;
	padding: .5rem;
}

.preview-image {
	position: relative;
	margin-bottom: .5rem;
	margin-right: .5rem;
	overflow: hidden;

}

.preview-image img {
	width: 180px;
	height: auto;
}

.preview-remove {
	opacity: 0;
}

.preview-image:hover .preview-remove {
	opacity: 1;
}

.preview-remove {
	width: 20px;
	height: 20px;
	position: absolute;
	right: 0;
	top: 0;
	font-weight: bold;
	background: rgba(255, 255, 255, .5);
	display: flex;
	align-items: center;
	justify-content: center;
	cursor: pointer;
	transition: opacity .22s; 
}

.preview-info {
	position: absolute;
	right: 0;
	bottom: 3px;
	left: 0;
	height: 25px;
	font-size: .8rem;
	background: rgba(255, 255, 255, .5);
	display: flex;
	align-items: center;
	justify-content: space-between;
	transition: bottom .22s;
	padding: 0 5px; 
	opacity: 0;
}

.preview-image:hover {
	opacity: 1;
	transform: opacity .22s;
}
